.whoweare{
    display: flex;
   height: 509px;
    /* background-color: #ff333c; */

    margin: 0px 50px;
   
  
}
.whoweare_heading{
    text-align: center;
}
.whoweare_content{
    /* flex: 0.6; */
    position:absolute;
    /* top: 70%; */
    font-size: 18px;
    font-family: 'Playfair Display', serif;
    text-align:justify;
}
.whoweare_inner_content::before{
    content: '';
    position: absolute;
    height: 80%;
    width: 100%;
    /* background-color: #25215e; */
    background-color: #ff333c;
    z-index: -1;
    opacity: 0.89;
}
.whoweare_inner_content{
    width: 800px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding:40px 50px;
    
    font-family: 'Playfair Display', serif;
  
    
}
.whoweare_img_inner{
    position:absolute;
    width: 600px;
    right: 0;
    z-index: -2;
    margin-top: 40px;
    margin-right: 30px;
   
   
 
}