    padding: 10px 10px;
    cursor: pointer;
    background-color:#25215e ;
    /* background-color:#ff333c  ; */
    color: white;
    border-radius: 40px;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    overflow: hidden;
    text-decoration: none;
    /* animation: wiggle 2s linear infinite; */
    /* box-shadow: 0 0 5px #ff0000,0 0 4px #ff0000,0 0 5px #ff0000; */
}
@keyframes wiggle {
  0%, 7% {
    transform: rotateZ(0);
  }
  15% {
    transform: rotateZ(-15deg);
  }
  20% {
    transform: rotateZ(10deg);
  }
  25% {
    transform: rotateZ(-10deg);
  }
  30% {
    transform: rotateZ(6deg);
  }
  35% {
    transform: rotateZ(-4deg);
  }
  40%, 100% {
    transform: rotateZ(0);
  }
}

.slider_btn:hover{
